.App {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
	/* text-align: center; */
	background-color: #333;
	direction: rtl;
}

.App-logo {
	animation: App-logo-spin infinite 20s linear;
	height: 40vmin;
	pointer-events: none;
}

.App-header {
	background-color: #282c34;
	// background-color: rgba(0, 0, 0, 0.6);
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

button {
	border: none;
	border-width: 0;
	background-color: transparent;
	color: #ddd;
	pointer-events: all;
	text-align: center;
	&:hover {
		color: white;
		background: #666;
	}
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
